h1,
h2,
p {
  text-align: center;
}
body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
}

.App {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
}

.title {
  margin-bottom: 20px;
}

.myface {
  width: 200px;
  height: auto;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
  transition: transform 0.5s;
}

.rotate {
  transform: rotate(360deg); /* クリック時の回転角度 */
}

.hobby {
  margin-bottom: 20px;
}

.image-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.image-container img {
  width: 300px;
  height: auto;
  border-radius: 10px;
  margin: 0 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.image-container img:hover {
  transform: scale(1.1); /* ホバー時にアイコンを拡大 */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* ホバー時に影を付ける */
}

.skills-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.skills-container img {
  width: 200px;
  height: auto;
  border-radius: 50%;
  margin: 0 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* 変化を滑らかにするトランジションを追加 */
}

.skills-container img:hover {
  transform: scale(1.1); /* ホバー時にアイコンを拡大 */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* ホバー時に影を付ける */
}
