.header {
  width: 100%;
  height: 80px;
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center; /* アイテムを中央に配置 */
}

.home:hover {
  background-color: #555;
}

.home,
.sns {
  margin-right: 25px;
  padding: 12px 24px;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease; /* 背景色のトランジションを追加 */
}

.home:hover,
.sns:hover {
  background-color: #555;
}
